<template>
  <div>
    <div class="login-container">

      <el-row class="top">
        <label  @click="loginRegisterSwitch" @mouseenter="mouseOn" @mouseleave="mouseLeave" class="float-right"
            :class="{'icon-znn-qiehuanhou': loginState,'icon-znn-qiehuanqian': registerState}"
            :style="switchStyle"></label>
      </el-row>

      <el-row class="top" v-show="forgetPwdState">
        <i  @click="returnLogin" @mouseenter="mouseOn" @mouseleave="mouseLeave" class="float-left icon-znn-fanhui " :style="switchStyle"  ></i>
      </el-row>

      <el-form class="login-form" :model="form" :rules="rules" ref="form">

      <!--登陆-->
      <div v-show="loginState">
          <el-form-item>
            <el-input placeholder="请输入用户名或邮箱" class="top" prefix-icon="icon-znn-denglu" v-model="form.username" clearable></el-input>
          </el-form-item>
          <el-form-item>
            <el-input placeholder="请输入密码"  type="password" class="top" prefix-icon="icon-znn-mima" v-model="form.password" clearable></el-input>
          </el-form-item>
          <el-form-item>
            <el-input  placeholder="请输入验证码" class="top" prefix-icon="icon-znn-yanzhengma" v-model="form.verificationCode" clearable>
              <i slot="suffix" @click="login"><img class="cursor-pointer verification-code" src="static/logo.png"/></i>
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-row class="top">
              <el-col > <el-button  @click="login" class="button" round>登陆</el-button></el-col>
              <label @mouseenter="mouseOn" @mouseleave="mouseLeave" @click="forgetPwdSwitch"
                     :style="switchStyle" >忘记密码？</label>
            </el-row>
          </el-form-item>
        <el-form-item>
          <el-row class="top">
            <el-col > <el-button  @click="loginRe" class="button" round>测试</el-button></el-col>
          </el-row>
        </el-form-item>
      </div>

        <!--注册-->
        <div v-show="registerState">
          <el-form-item  prop="username">
            <el-input placeholder="请输入用户名" class="top" prefix-icon="icon-znn-denglu" v-model="form.username" autocomplete="off" clearable></el-input>
          </el-form-item>
          <el-form-item  prop="password">
            <el-input placeholder="请输入密码" type="password" class="top" prefix-icon="icon-znn-mima" v-model="form.password" autocomplete="off" clearable></el-input>
          </el-form-item>
          <el-form-item  prop="checkPassword">
            <el-input placeholder="确认密码" type="password" class="top" prefix-icon="icon-znn-zaicishurumima" v-model="form.checkPassword" autocomplete="off" clearable></el-input>
          </el-form-item>
          <el-form-item  prop="email">
            <el-input placeholder="请输入邮箱" class="top" prefix-icon="icon-znn-youxiang" v-model="form.email" autocomplete="off" clearable></el-input>
          </el-form-item>
          <el-form-item prop="verificationCode">
            <el-input  placeholder="请输入验证码" class="top" prefix-icon="icon-znn-yanzhengma"
                       v-model="form.verificationCode">
              <label  @click="login"  @mouseenter="mouseOn" @mouseleave="mouseLeave" class="cursor-pointer" :style="switchStyle" slot="suffix">
                获取验证码
              </label>
            </el-input>
          </el-form-item>
          <el-form-item >
            <el-row class="top">
              <el-col> <el-button  class="button" round>注册</el-button></el-col>
            </el-row>
          </el-form-item>
        </div>

        <!--忘记密码-->
        <div v-show="forgetPwdState">
          <el-form-item >
            <el-input placeholder="请输入邮箱" class="top" prefix-icon="icon-znn-youxiang" v-model="form.email" clearable></el-input>
          </el-form-item>
          <el-form-item prop="verificationCode">
            <el-input  placeholder="请输入验证码" class="top" prefix-icon="icon-znn-yanzhengma"
                       v-model="form.verificationCode">
              <label  @click="login" @mouseenter="mouseOn" @mouseleave="mouseLeave" class="cursor-pointer" :style="switchStyle" slot="suffix">
                获取验证码
              </label>
            </el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input placeholder="请输入新密码" type="password" class="top" prefix-icon="icon-znn-mima" v-model="form.password" autocomplete="off" clearable></el-input>
          </el-form-item>
          <el-form-item prop="checkPassword">
            <el-input placeholder="确认新密码" type="password" class="top" prefix-icon="icon-znn-zaicishurumima" v-model="form.checkPassword" autocomplete="off" clearable></el-input>
          </el-form-item>
          <el-form-item>
            <el-row class="top">
              <el-col> <el-button  class="button" round>重置密码</el-button></el-col>
            </el-row>
          </el-form-item>
        </div>
      </el-form>
    </div>

    <!--粒子特效-->
    <div class="particles">
      <vue-particles class="particles"
        color="#dedede"
        :particleOpacity="0.7"
        :particlesNumber="100"
        shapeType="circle"
        :particleSize="4"
        linesColor="#dedede"
        :linesWidth="1"
        :lineLinked="true"
        :lineOpacity="0.5"
        :linesDistance="150"
        :moveSpeed="3"
        :hoverEffect="true"
        hoverMode="grab"
        :clickEffect="true"
        clickMode="push"
      >
      </vue-particles>
    </div>

  </div>
</template>

<script>

  import {validate} from '@/util/validator'

export default {
  data () {
    var validateUsername = (rule, value, callback) => {
      if (!value) {
        callback(new Error('用户名不能为空'));
      }else if (value.length > 18 || value.length < 4){
        callback(new Error('用户名长度2-18个字符'));
      }else if (!validate(value)) {
          callback(new Error('用户名只能由字母、数字和下划线,且以字母开头'));
      }else {
        callback();
      }
    };
    var validatePass = (rule, value, callback) => {
      if (!value) {
        callback(new Error('密码不能为空'));
      } else if (value.length > 18 || value.length < 6) {
        callback(new Error('密码长度6-18个字符'));
      }else {
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (!value) {
        callback(new Error('密码不能为空'));
      } else if (value !== this.form.password) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    var validateEmail = (rule, value, callback) => {
      if (!value) {
        callback(new Error('请输入邮箱地址'));
      } else if (value ) {
        callback();
      }
    };
    return {

      loginState: true,
      registerState: false,
      forgetPwdState: false,

      //通知
      notify: undefined,

      switchStyle: {
        cursor: 'pointer',
        color: '#8a8a8a'
      },

      form: {
        username: '',
        password: '',
        checkPassword: '',
        email: '',
        verificationCode: '',
      },

      rules: {
        username: [
          { validator: validateUsername, trigger: 'blur' }
        ],
        password: [
          { validator: validatePass, trigger: 'blur' }
        ],
        checkPassword: [
          { validator: validatePass2, trigger: 'blur' }
        ],
        email: [
          // { validator: validateEmail, trigger: 'blur' },
          { required: true, message: '邮箱不能为空', trigger: 'blur' },
          { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
        ]
      }
    }
  },
  methods: {

    login () {
      var loginForm = {
        username : this.form.username,
        password : this.form.password
      }
      this.axios.post("/login",this.$qs.stringify(loginForm),{
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(response => {
        console.log(response.data)
      })
    },
    loginRe () {

      this.axios.get("/login",{},{
      }).then(response => {
        console.log(response.data)
      })
    },
    loginRegisterSwitch () {
      this.loginState = !this.loginState
      this.registerState = !this.registerState
      if(this.registerState){
        var tips = '用户名由字母数字和下划线组成，以字母开头；用户名4-18个字符,密码6-18个字符！'
        this.notify = this.prompt(tips)
      }
      if(!this.registerState){
        this.notify.close();

      }
      this.resetForm('form')
    },
    mouseOn () {
      this.switchStyle.color = '#333333'
    },
    mouseLeave () {
      this.switchStyle.color = '#8a8a8a'
    },
    forgetPwdSwitch () {
      this.forgetPwdState = !this.forgetPwdState
      this.loginState = false
      this.registerState = false
      this.switchStyle.color = '#8a8a8a'
      if(this.forgetPwdState){
        var tips = '亲，验证码5分钟内有效，过时不候哦!'
        this.notify = this.prompt(tips)
      }
      this.resetForm('form')
    },
    returnLogin () {
      this.forgetPwdState = !this.forgetPwdState
      this.loginState = true
      this.registerState = false
      this.switchStyle.color = '#8a8a8a'
      if(!this.forgetPwdState){
        this.notify.close();
      }
      this.resetForm('form')
    },
    prompt(mess) {
      return this.$notify({
        title: '提示',
        message: mess,
        duration: 6000,
      });
    },
    resetForm (formName) {
      this.$refs[formName].resetFields();
    }
  }
}
</script>

<style scoped>
.login-container {
    margin: 5% auto 0;
    background-color: #fff;
    border-radius: 4px;
    box-shadow: 0 0 8px rgba(0,0,0,.1);
    display: inline-block;
}
.top{
  z-index: 999;
}
.login-form{
  width: 300px;
  padding: 25px 100px;
  background-color: #fff;
  border-radius: 4px;
  vertical-align: middle;
  display: inline-block;
}
.button{
  width: 300px;
}
.float-right{
  float:right;
  margin: 10px 10px 0px 0px;
  position: relative;
  cursor:pointer
}

.float-left{
  float:left;
  margin: 10px 0px 0px 10px;
  position: relative;
  cursor:pointer
}

.cursor-pointer{
  cursor:pointer
}

.verification-code{
  width: 50px;
  height: 40px;
}

.particles{
  /*background-image: url(/static/img/sky.871d198.jpg);*/
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 120%;
}
</style>



